.popup__container {
  position: relative;
  width: 430px;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: #ffffff;
  color: #000000;
  padding: 34px 36px 37px;
  text-align: center;
}

@media screen and (max-width: 540px) {
  .popup__container {
    width: calc(100% - 38px);
    padding: 25px 22px;
  }
}
