.profile__edit-avatar {
  padding: 0;
  border: 0;
  background: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: background-color .2s linear;
  outline: none;
}

.profile__edit-avatar:hover{
  background: url(../../../images/editAvatar.svg) center no-repeat rgba(0, 0, 0, .8);
}