.header {
  padding: 45px 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (max-width: 1024px) {
  .header {
    padding-left: 19px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 28px 27px 32px;
  }
}